<template>
  <div id="text-document">
    <div class="left">
      <div class="title">
        <h2 style="text-align:center">文字文档</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺：</h3>
        <div class="shop-list">
          <!-- 菜单 -->
          <el-menu
            default-active="0-0-0"
            class="el-menu-vertical-demo"
            unique-opened
          >
            <el-submenu
              :index="index + ''"
              v-for="(item, index) in menuPlatFormList"
              :key="index"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="onceClick(index)"
                  >{{ item }}</span
                >
              </template>
              <el-submenu
                :index="index + '-' + index2"
                v-for="(item2, index2) in showCountryList"
                :key="index2"
              >
                <template slot="title">
                  <span
                    style="display:inline-block;width:100%"
                    @click="secondClick(index2)"
                    >{{ item2 }}</span
                  >
                </template>
                <el-menu-item
                  :index="index + '-' + index2 + '-' + index3"
                  v-for="(item3, index3) in lastSiteList"
                  :key="index3"
                  @click="thirdClick(item, item2, item3)"
                >
                  <span>{{ item3 }}</span>
                </el-menu-item>
              </el-submenu>
            </el-submenu>
          </el-menu>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="info">
        <span>站点：{{ checkedSite }}</span>
        <span>国家：{{ checkedCountry }}</span>
      </div>
      <div class="table">
        <div class="button">
          <el-button type="primary" size="mini" @click="addTextDoc"
            >新增</el-button
          >
          <el-button type="primary" size="mini" @click="editTextDoc"
            >编辑</el-button
          >
          <el-button type="danger" size="mini" @click="removeTextDoc"
            >删除</el-button
          >
        </div>
        <el-table
          :data="textDocList"
          border
          style="width: 100%"
          @selection-change="handleSelectionChange"
          ref="textDocRef"
          max-height="700"
        >
          <el-table-column type="selection"></el-table-column>
          <el-table-column
            type="index"
            label="序号"
            width="70"
          ></el-table-column>
          <el-table-column prop="1" label="站点" width="70"></el-table-column>
          <el-table-column prop="2" label="国家" width="70"></el-table-column>
          <el-table-column prop="3" label="sku" width="200"></el-table-column>
          <el-table-column prop="4" label="标题"></el-table-column>
          <el-table-column prop="5" label="五点描述">
            <template slot-scope="scope">
              <el-popover
                placement="top"
                width="400"
                trigger="click"
                :content="scope.row[5]"
              >
                <span class="five-point" slot="reference">{{
                  scope.row[5]
                }}</span>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>

    <!-- 新增对话框 -->
    <el-dialog
      title="新增文字文档"
      :visible.sync="addTextDialogVisible"
      width="50%"
      center
      @close="addTextDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <!-- 表单区域 -->
      <el-form
        :model="addTextForm"
        :rules="formRules"
        ref="addTextRef"
        label-width="100px"
      >
        <el-form-item label="平台" prop="platform">
          <el-select v-model="addTextForm.platform" placeholder="请选择">
            <el-option
              v-for="(item, index) in platformList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家" prop="country">
          <el-select
            v-model="addTextForm.country"
            placeholder="请选择"
            @change="countrySelectChange"
          >
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点" prop="site">
          <el-select
            v-model="addTextForm.site"
            placeholder="请选择"
            @change="siteSelectChange"
          >
            <el-option
              v-for="(item, index) in siteList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="sku" prop="sku">
          <el-select v-model="addTextForm.sku" placeholder="请选择">
            <el-option
              v-for="(item, index) in skuList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input
            v-model="addTextForm.title"
            type="textarea"
            :rows="3"
          ></el-input>
        </el-form-item>
        <el-form-item label="五点描述" prop="fivePoint">
          <el-input
            v-model="addTextForm.fivePoint"
            type="textarea"
            :rows="12"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addTextDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addTextConfirm">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑对话框 -->
    <el-dialog
      title="编辑文字文档"
      :visible.sync="editTextDialogVisible"
      width="50%"
      center
      @close="editTextDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <!-- 表单区域 -->
      <el-form
        :model="editTextForm"
        :rules="formRules"
        ref="editTextRef"
        label-width="100px"
      >
        <el-form-item label="站点">
          <el-input v-model="editTextForm.site" readonly></el-input>
        </el-form-item>
        <el-form-item label="国家">
          <el-input v-model="editTextForm.country" readonly></el-input>
        </el-form-item>
        <el-form-item label="sku">
          <el-input v-model="editTextForm.sku" readonly></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input
            v-model="editTextForm.title"
            type="textarea"
            :rows="3"
          ></el-input>
        </el-form-item>
        <el-form-item label="五点描述" prop="fivePoint">
          <el-input
            v-model="editTextForm.fivePoint"
            type="textarea"
            :rows="12"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editTextDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editTextConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 文字文档列表
      textDocList: [],
      // 选中多选框数量
      selectNum: 0,
      // 选择的行数据
      selection: [],
      // 新增所需数据
      addTextData: [],
      // 店铺列表
      shopList: [],
      // 新增对话框所需数据
      addTextDialogVisible: false,
      addTextForm: {
        platform: "",
        site: "",
        country: "",
        sku: "",
        title: "",
        fivePoint: ""
      },
      // 编辑对话框所需数据
      editTextDialogVisible: false,
      editTextForm: {
        platform: "",
        site: "",
        country: "",
        sku: "",
        title: "",
        fivePoint: "",
        id: ""
      },
      formRules: {
        platform: [
          { required: true, message: "请选择平台", trigger: "change" }
        ],
        site: [{ required: true, message: "请选择站点", trigger: "change" }],
        country: [{ required: true, message: "请选择国家", trigger: "change" }],
        sku: [{ required: true, message: "请选择sku", trigger: "change" }],
        title: [{ required: true, message: "请输入标题", trigger: "blur" }],
        fivePoint: [
          { required: true, message: "请输入五点描述", trigger: "blur" }
        ]
      },
      // 下拉框列表
      countryList: [],
      platformList: ["Amazon"],
      siteList: [],
      skuList: [],
      // 选中的下拉框
      selectPlatform: "",
      selectCountry: "",
      selectSite: "",

      // 左侧菜单点击显示对应下级菜单
      // menuPlatformList:[],
      // menuCountryList: [],
      // menuSiteList: [],
      // // 选中的左侧菜单
      // platform: "",
      // country: "",

      // 下拉框改变更新的列表
      // newPlatformList: [],
      // newCountryList: [],
      // newSkuList: [],
      // newSiteList: []
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;

        // 页面刷新 先默认展示胤佑的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // 获取下拉框数据
    getTextSelectList() {
      const selectList = {};
      this.$api.getTextSelectList(selectList).then(res => {
        console.log(res);
        this.countryList = res.data.country_list;
        this.platformList = res.data.platform_list;

        this.siteList = res.data.site_list;
        this.skuList = res.data.sku_list;
      });
    },
    // 监控多选框事件
    handleSelectionChange(selection) {
      this.selectNum = selection.length;
      this.selection = selection;
    },
    // 批量删除站点信息
    removeTextDoc() {
      if (this.selectNum == 0) {
        this.$message.closeAll();
        return this.$message.info("请选择商品");
      }
      let id = [];
      for (let item of this.selection) {
        id.push(item[0]);
      }
      console.log(id);

      this.$confirm("此操作将永久删除这些记录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteText(id).then(res => {
            console.log(res);
            this.thirdClick(
              this.checkedPlatform,
              this.checkedCountry,
              this.checkedSite
            );
            this.$message.success("删除成功");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 新增
    addTextDoc() {
      this.addTextDialogVisible = true;
    },
    // 编辑
    editTextDoc() {
      if (this.selectNum == 0) {
        this.$message.closeAll();
        return this.$message.info("请选择商品");
      } else if (this.selectNum > 1) {
        this.$message.closeAll();
        return this.$message.info("一次只允许编辑一个");
      }
      // 为编辑对话框一些数据赋值
      this.editTextForm.site = this.selection[0][1];
      this.editTextForm.country = this.selection[0][2];
      this.editTextForm.sku = this.selection[0][3];
      this.editTextForm.id = this.selection[0][0];
      this.editTextForm.title = this.selection[0][4];
      this.editTextForm.fivePoint = this.selection[0][5];
      this.editTextDialogVisible = true;
    },
    // 确认新增文档
    addTextConfirm() {
      this.$refs.addTextRef.validate(state => {
        if (!state) return;
        let formData = new FormData();
        formData.append("platform", this.addTextForm.platform);
        formData.append("country", this.addTextForm.country);
        formData.append("site", this.addTextForm.site);
        formData.append("sku", this.addTextForm.sku);
        formData.append("title", this.addTextForm.title);
        formData.append("describe", this.addTextForm.fivePoint);
        this.$api.addText(formData).then(res => {
          console.log(res);
          this.$message.success("新增成功");
          this.thirdClick(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite
          );
          this.addTextDialogVisible = false;
        });
      });
    },
    // 确认编辑文档
    editTextConfirm() {
      this.$refs.editTextRef.validate(state => {
        if (!state) return;
        let formData = new FormData();
        formData.append("text_id", this.editTextForm.id);
        formData.append("title", this.editTextForm.title);
        formData.append("describe", this.editTextForm.fivePoint);
        this.$api.editText(formData).then(res => {
          console.log(res);
          // this.getTextList();
          this.thirdClick(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite
          );
          this.editTextDialogVisible = false;
          this.$message.success("编辑成功");
        });
      });
    },
    onceClick(index) {
      // if(this.platformOnce[index] == "") {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
      // }
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.stockCalculationList = [];
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      let obj = {
        platform,
        country,
        site,
        page: 1
      };
      this.$api.textQueryInfo(obj).then(res => {
        console.log(res);
        this.textDocList = res.data.data;
      });
    },
    // 新增对话框关闭
    addTextDialogClose() {
      this.$refs.addTextRef.resetFields();
      // this.getTextSelectList();
    },
    // 编辑对话框关闭
    editTextDialogClose() {
      this.$refs.editTextRef.resetFields();
    },
    // 国家下拉框选中触发
    countrySelectChange(val) {
      console.log(val);
      // let obj = {};
      // for (let i in this.addTextForm) {
      //   if (
      //     this.addTextForm[i].length > 0 &&
      //     i != "title" &&
      //     i != "fivePoint"
      //   ) {
      //     obj[i] = this.addTextForm[i];
      //   } else if (
      //     this.addTextForm[i].length == 0 &&
      //     i != "title" &&
      //     i != "fivePoint"
      //   ) {
      //     obj[i] = "";
      //   } else {
      //     continue;
      //   }
      // }
      // console.log(obj);
      // this.$api.getTextSelectList(obj).then(res => {
      //   console.log(res);
      //   for (let i in obj) {
      //     if (obj[i] == "") {
      //       console.log(i);
      //       this[i + "List"] = res.data[i + "_list"];
      //     }
      //   }
      // });
      this.selectCountry = val;
      this.$api.getSiteList(val).then(res => {
        console.log(res);
        this.siteList = res.data.data;
      });
    },
    // 站点下拉框触发
    siteSelectChange(val) {
      this.selectSite = val;
      console.log(val);
      this.$api
        .getTextSelectList(
          this.selectPlatform,
          this.selectCountry,
          this.selectSite
        )
        .then(res => {
          console.log(res);
          this.skuList = res.data.sku_list;
        });
    },
    // 行点击触发
    textRowClick(row) {
      this.$refs.textDocRef.toggleRowSelection(row);
    },
    // 分页相关
    handleCurrentChange(val) {
      console.log(val);
      let obj = {
        platform: this.checkedPlatform,
        country: this.checkedCountry,
        site: this.checkedSite,
        page: val
      };
      this.$api.textQueryInfo(obj).then(res => {
        console.log(res);
        this.textDocList = res.data.data;
      });
    },

    // 获取国家下拉框列表
    getCountryList() {
      this.$api.getCountryList().then(res => {
        console.log(res);
        this.countryList = res.data.data;
      });
    }
  },
  created() {
    // this.getTextSelectList();
    this.getMenuList();
    this.getCountryList();
  },
  mounted() {}
};
</script>
<style scoped>
#text-document {
  width: 100vw;
  height: 92vh;
  position: relative;
}
.title {
  margin-top: 2vh;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.info span {
  margin-right: 12px;
}
.table .button {
  margin: 12px 0 12px 0;
}
.el-pagination {
  margin-top: 24px;
}
.five-point {
  white-space: pre-wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
</style>



